import React from 'react';

import RGBtoHSL from './color';

import arrow from 'icons/carret.svg';

/**
 * Parameters that can be entered after initializing flexcards.js.
 * @interface FlexCardsParams
 */

interface FlexCardsParams extends React.HTMLAttributes<HTMLDivElement> {
    component?: 'default' | 'images';
    delay?: number;
    indexType?: 'numbers' | 'dots';
    stop?: boolean;
    theme?: string;
    timer?: boolean;
};

function getTheme(color: string) {
    // Get color theme
    color = color.replace('#', '');
    color = color.replace(/^([a-f\d])([a-f\d])([a-f\d])$/i, (_m, r, g, b) => r+r+g+g+b+b);

    const result = /^([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(color);

    let rgb = result // Parse numbers from hexadecimal to decimal
        ? [parseInt(result[1], 16), parseInt(result[2], 16), parseInt(result[3], 16)]
        : [0, 0, 0],
        filter = new RGBtoHSL(rgb[0], rgb[1], rgb[2]).solve();

    return { rgb: "#" + color, filter };
};

function setClass (...tokens: Array<string | undefined>) {
    let result = tokens.map(a => typeof a === 'string' ? a : "").join(' ').replace(/  +/g, " ");
    return result.endsWith(" ") ? result.slice(0, -1) : result;
}

export const Component: React.FC<React.HTMLAttributes<HTMLElement>> = params => (
    <article {...params} className={setClass('flexcards__card', params.className)}>
        {params.children}
    </article>
);

export const Carousel: React.FC<FlexCardsParams> = ({
    component = 'default',
    delay = 6000,
    indexType = 'dots',
    stop = false,
    theme = '#666',
    timer = false,
  ...params
}) => {
    var interval = setInterval(() => void 0, delay),
        carousel = document.createElement('div'),
        refreshTime = 250,
        timeElapsed = 0,
        index = 0;

    const colors = getTheme(theme);
    const id = Array.from(new Uint8Array(10)).map(() => "abcdefghijklmnopqrstuvwxyz"
        [Math.floor(Math.random()*26)]
        [Math.round(Math.random()) ? 'toUpperCase' : 'toLowerCase']
        ()).join('');

    // Get slides
    var slides = Array.from(carousel.querySelectorAll(component==='default'?'article':'img')),
        { length } = slides;

    const dotClick = (key: number) => {
        let count = key - index;

        for (let i = 0; i < Math.abs(count); i++) {
            if (count < 0) carousel.querySelector('.left');
            else if (count > 0) carousel.querySelector('.right');
        }
    };

    const render = (step = 0) => {
        let content = carousel.querySelector<HTMLDivElement>('.flexcards__content')
            || document.createElement('div');

        // Re-init time elapsed
        timeElapsed = 0;

        const updateContent = () => {
            // put the animation in this function
            content.querySelectorAll('article').forEach(el => {
                el.classList.remove('animate');
                el.remove();
            });

            // Add items in a specific order
            content.append(
                slides[(index - 1 < 0 ? length : index) - 1],
                slides[index],
                slides[index + 1 >= length ? 0 : index + 1],
            );

            content.scroll({ left: content.clientWidth });
            slides[index].classList.add('animate');
            content.addEventListener('scroll', onScroll);

            let timer = carousel.querySelector<HTMLDivElement>('.flexcards__timer');
            timer && timer.style.setProperty('--percentage', '0');
        }

        if (Math.abs(step)) { // If step != 0 (have to scroll)
            content.removeEventListener('scroll', onScroll);
            content.scroll({
                left: content.clientWidth * (1 + step),
                behavior: 'smooth',
            });

            setTimeout(updateContent, 550);
        } else updateContent();

        // Toggle index
        if (indexType === 'numbers') {
            let el = carousel.querySelector('.flexcards__index .flexcards__count');
            el && (el.textContent = Number(index + 1).toString());
        } else {
            content.querySelectorAll<HTMLSpanElement>('.flexcards__index > span')
                .forEach((dot, key) => dot.classList[index === key ? 'add' : 'remove']('current'));
        }

        // Re-set "auto-scroll"
        interval = setInterval(() => (
            carousel.querySelector<HTMLButtonElement>('.flexcards__arrow.right')?.click()
        ), delay);
    };

    const onScroll = (e: Event) => {
        let content = e.currentTarget as HTMLDivElement,
            calc = 100 * (content.scrollLeft / content.scrollWidth - 1);

        if (calc >= 5) carousel.querySelector<HTMLButtonElement>('.right')?.click();
        else if (calc <= -5) carousel.querySelector<HTMLButtonElement>('.left')?.click();
    };

    const onArrowClick = (e: React.MouseEvent<HTMLButtonElement>) => {
        clearInterval(interval);

        let arrow = e.currentTarget,
            initialIndex = index;

        index += Number(arrow.classList.contains('left')) * -2 + 1;

        // Check if index is a num between 0 and length
        if (index < 0) {
            index = length - 1;
            initialIndex = length;
        } else if (index >= length) {
            index = 0;
            initialIndex = -1;
        }

        render(index - initialIndex);
        arrow.blur();
    };

    React.useEffect(() => {
        carousel = (document.querySelector('.flexcards__' + id)
            || document.createElement('div')) as HTMLDivElement;

        // Index color theme
        carousel.querySelector<HTMLDivElement>(".flexcards__index")
            ?.style.setProperty('--theme', colors.rgb);

        const timerDisplay = carousel.querySelector<HTMLDivElement>(".flexcards__timer");

        // Timer
        if (timer && timerDisplay) {
            let iterations = delay / refreshTime * 2;

            timerDisplay.style.setProperty('--theme', colors.rgb)
            timerDisplay.style.transitionDuration = `${iterations}ms`;

            // Animation function
            setInterval(() => timerDisplay.style.setProperty('--percentage', (
                timeElapsed / delay * 200
            ).toString()), iterations*1e5);
        }

        // Get time elpased
        let adder = delay / refreshTime;
        setInterval(() => timeElapsed += adder, adder);

        // Set data-id attributes to each slides
        slides.forEach((slide, key) => {
            slide.classList.add('flexcards__card');
            if (component === 'images') slide.classList.add('flexcards__image');
            slide.setAttribute('data-id', key.toString());
        });

        // Stop function
        stop && clearInterval(interval);

        // Seems strange, but removing this line, the code won't work properly
        carousel.querySelector('.flexcards__content')?.addEventListener('scroll', onScroll);
    });

	return (
		<div
            {...params}
            className={setClass('flexcards__wrapper', 'flexcards__'+id, params.className)}
        >
			<div className="flexcards__container flexcards__carousel">
                {timer ? <span className="flexcards__timer"></span> : ""}

                <button
                    className="flexcards__arrow left"
                    type="button"
                    style={{ filter: colors.filter }}
                    onClick={onArrowClick}
                >
                    <img className="flexcards__carret" src={arrow} alt="Toggle left" />
                </button>

                <div className="flexcards__content">
                    {/* Childrens */}
                    {params.children}
                </div>

                <button
                    className="flexcards__arrow right"
                    type="button"
                    style={{ filter: colors.filter }}
                    onClick={onArrowClick}
                >
                    <img className="flexcards__carret" src={arrow} alt="Toggle right" />
                </button>
			</div>

            <div className="flexcards__index">
                {indexType === 'numbers' ? (
                    <React.Fragment>
                        <span className="flexcards__count">1</span>
                        <span> / </span>
                        <span className="flexcards__limit">{length}</span>
                    </React.Fragment>
                ) : slides.map((_, key) => (
                    <span
                        key={key}
                        className="flexcards__dot"
                        onClick={() => dotClick(key)}
                    ></span>
                ))}
            </div>
		</div>
	);
};

export const AlternateCarousel: React.FC<FlexCardsParams> = ({
    component = "default",
    delay = 6000,
    indexType = 'dots',
    stop = false,
    theme = "#666",
    timer = false,
    ...params
}) => {
    const id = Array.from(new Uint8Array(10)).map(() => "abcdefghijklmnopqrstuvwxyz"
        [Math.floor(Math.random()*26)]
        [Math.round(Math.random()) ? 'toUpperCase' : 'toLowerCase']
        ()).join('');

    React.useEffect(() => {
        if (!document.getElementById('flexcardsjs-script')) {
            let script = document.createElement('script'),
                source = "https://theredminetheredmine.github.io";

            script.id = 'flexcardsjs-script';
            script.src = source + "flexcards.js/dist/flexcards.min.js";
            script.setAttribute('data-host', source);

            document.head.appendChild(script);
        }

        setTimeout(function () { // @ts-ignore
            const flexcards = new FlexCards("." + id, {
                component, indexType, theme, timer
            }); // @ts-ignore
            flexcards.carousel();
        }, 500);
    }, [id]);

    return (
        <div {...params} className={setClass(id, params.className)}>
            {params.children}
        </div>
    );
}

export default { Carousel };
